.Home {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.home-box {
    width: fit-content;
    margin-top: 1.5cm;
    display: flex;
    flex-direction: column;
   align-items: center;
}
.bio{
    font-size: 25px;
    font-family: Titillium Web ;
    margin-top: -1.2cm;
    align-items: center;
}
.name {
    font-weight: 1000;
    font-size: 100px;
    font-family: changa;
    height: fit-content;
    background: linear-gradient(90deg, rgb(230, 65, 87) 0%, rgb(65, 93, 230) 100%);
    background-clip: text;
    -webkit-background-clip: text;
    color: transparent;
    margin: 0;
    padding: 0; /* Add this line to remove padding */
}


